<template>
  <div>
    <v-card class="table filter">
      <v-card-title class="table_head">
        <div class="table_title mb-1">Filter:</div>
        <div class="table_title mb-2">
          <v-text-field
            v-model="name"
            label="Name"
            hide-details
            solo-inverted
            flat
            dense
            class="table_search mt-1"
          ></v-text-field>
          <v-menu
            v-model="menuStart"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                color="white"
                append-icon="mdi-calendar"
                v-model="dateStart"
                label="Start date"
                readonly
                dense
                flat
                solo-inverted
                clearable
                hide-details
                v-bind="attrs"
                v-on="on"
                class="datepicker mt-1"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="dateStart"
              @input="menuStart = false"
              header-color="blue darken-3"
            ></v-date-picker>
          </v-menu>
          <v-menu
            v-model="menuEnd"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                color="white"
                append-icon="mdi-calendar"
                v-model="dateEnd"
                label="End date"
                readonly
                dense
                flat
                solo-inverted
                clearable
                hide-details
                v-bind="attrs"
                v-on="on"
                class="datepicker mt-1"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="dateEnd"
              @input="menuEnd = false"
              header-color="blue darken-3"
            ></v-date-picker>
          </v-menu>
          <div class="table_box table_choise mt-1">
            Product:
            <v-checkbox
              label="Standart"
              color="primary"
              hide-details
              dense
              class="mr-1 ml-1"
            ></v-checkbox>
            <v-checkbox
              label="Plus"
              color="primary"
              hide-details
              dense
              class="mr-1"
            ></v-checkbox>
            <v-checkbox
              label="Premium"
              color="primary"
              hide-details
              dense
              class="mr-1"
            ></v-checkbox>
            <v-checkbox
              label="Gold"
              color="primary"
              dense
              hide-details
              class="mr-1"
            ></v-checkbox>
          </div>
        </div>
        <div class="table_title">
          <v-text-field
            v-model="code"
            label="Code"
            hide-details
            dense
            solo-inverted
            flat
            class="table_search"
          ></v-text-field>
        </div>
        <div class="table_title justify-center">
          <v-spacer></v-spacer>
          <div class="table_box table_box_buttons">
            <v-btn color="blue darken-3" class="mr-3 white--text">
              Search
            </v-btn>
            <v-btn
              color="blue darken-3"
              class="white--text"
              @click="resetInput()"
            >
              Reset
            </v-btn>
          </div>
          <v-spacer></v-spacer>
          <div class="table_box table_box_checkbox">
            <v-checkbox
              label="Apply current search filter"
              color="primary"
              hide-details
              dense
              class="mr-2 table_checkbox_current"
            ></v-checkbox>
            <v-btn @click="showDialogExcel()" x-small>
              Excel file Download
            </v-btn>
          </div>
        </div>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="$store.state.purchaseHistory"
        :search="name"
        class="table_data"
      >
        <template v-slot:top>
          <v-dialog v-model="dialogExcel" max-width="400px">
            <v-card>
              <v-container>
                <div
                  class="card_title text-center mb-8"
                  v-text="titleDialogExcel"
                ></div>
              </v-container>

              <v-card-actions class="justify-center">
                <v-btn color="primary" @click="closeDialogExcel()">
                  Confirm
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialog" max-width="800px">
            <v-card>
              <v-card-title class="text-h5">
                <span>Details</span>
              </v-card-title>

              <v-container>
                <v-row class="justify-center">
                  <v-col cols="12" sm="8" md="8">
                    <div class="text-subtitle-1 card_title text-center">
                      Charging Information
                    </div>
                    <div class="card_box mb-6">
                      <div v-if="transactionId !== ''" class="card_subtitle">
                        Transaction ID
                      </div>
                      <div
                        v-if="transactionId !== ''"
                        class="card_value"
                        v-text="transactionId"
                      ></div>
                      <div
                        v-if="transactionId === ''"
                        style="display: none"
                        class="card_subtitle"
                      >
                        Transaction ID
                      </div>
                      <div
                        v-if="transactionId === ''"
                        style="display: none"
                        class="card_value"
                        v-text="transactionId"
                      ></div>
                      <div class="card_subtitle">Name</div>
                      <div class="card_value" v-text="nameDialog"></div>
                      <div class="card_subtitle">Date</div>
                      <div class="card_value" v-text="datePayment"></div>
                      <div class="card_subtitle">Product</div>
                      <div class="card_value" v-text="productStatus"></div>
                      <div class="card_subtitle">Charging Credit</div>
                      <div class="card_value" v-text="chargingCredit"></div>
                      <div class="card_subtitle">Hoarding Credit</div>
                      <div class="card_value" v-text="hoardingCredit"></div>
                    </div>

                    <div class="text-subtitle-1 card_title text-center">
                      Payment Information
                    </div>
                    <div class="card_box mb-6">
                      <div class="card_subtitle">Method of payment</div>
                      <div class="card_value" v-text="methodPayment"></div>
                      <div class="card_subtitle">Amount of payment</div>
                      <div class="card_value" v-text="amountPayment"></div>
                    </div>
                  </v-col>
                </v-row>
              </v-container>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn outlined @click="closeDialog()"> List </v-btn>
                <v-btn outlined @click="$router.push('/credit/history')">
                  Credit History
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon class="mr-2" @click="showDialog(item)">
            mdi-information-outline
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      code: "",
      headers: [
        { text: "Code", align: "start", value: "code" },
        { text: "Date", value: "date" },
        { text: "Name", value: "name" },
        { text: "Product", value: "product" },
        { text: "Charge", value: "charge" },
        { text: "Hoarding Credit", value: "hoarding" },
        { text: "Payment", value: "payment" },
        { text: "Deposited", value: "deposited" },
        { text: "Transaction ID", value: "id" },
        { text: "Info", value: "actions", sortable: false },
      ],
      dateStart: "",
      dateEnd: "",
      menuStart: false,
      menuEnd: false,
      dialog: false,
      titleDialogExcel: "",
      dialogExcel: false,
      transactionId: "",
      nameDialog: "",
      datePayment: "",
      productStatus: "",
      chargingCredit: "",
      hoardingCredit: "",
      methodPayment: "",
      amountPayment: "",
    };
  },

  methods: {
    resetInput() {
      let inputCheckbox = document.querySelectorAll(
        ".table_head .v-input--selection-controls__input input"
      );

      inputCheckbox.forEach((element) => {
        if (element.checked) {
          element.click();
        }
      });

      this.name = "";
      this.code = "";
      this.dateStart = new Date(
        Date.now() - new Date().getTimezoneOffset() * 60000
      )
        .toISOString()
        .substr(0, 10);
      this.dateEnd = new Date(
        Date.now() - new Date().getTimezoneOffset() * 60000
      )
        .toISOString()
        .substr(0, 10);
    },
    showDialog(item) {
      this.dialog = true;

      this.transactionId = item.id;
      this.nameDialog = item.name;
      this.datePayment = item.date;
      this.productStatus = item.product;
      this.chargingCredit = item.charge;
      this.hoardingCredit = item.hoarding;
      if (item.method === "") {
        this.methodPayment = "-";
      } else {
        this.methodPayment = item.method;
      }
      if (item.method === "") {
        this.amountPayment = "-";
      } else {
        this.amountPayment = item.payment;
      }
    },
    closeDialog() {
      this.dialog = false;
    },
    showDialogExcel() {
      let isChecked = document.querySelector(".table_checkbox_current input");
      if (isChecked.checked) {
        this.titleDialogExcel =
          "Do you want to get the current search results in Excel?";
      } else {
        this.titleDialogExcel =
          "Do you want to get the entire data into Excel?";
      }
      this.dialogExcel = true;
    },
    closeDialogExcel() {
      this.dialogExcel = false;
    },
  },
};
</script>
